import React, { Fragment } from "react";

import { MemberList } from "assets/data/Member";

const Home = () => {
  const rows = [];

  for (const [key, value] of Object.entries(MemberList)) {
    rows.push(
      <tr key={key}>
        <th scope="row">{key}</th>
        <td>{value.신분}</td>
        <td>{value.유물5}</td>
        <td>{value.천왕퀘}</td>
      </tr>
    );
  }
  return (
    <Fragment>
      <div className="jumbotron">
        <div className="container">
          <h1 className="display-4">참여 현황</h1>
          <p className="lead">매일 한국시간 저녁 5시 전후로 업데이트 예정</p>
        </div>
      </div>
      <div className="container">
        <div className="table-responsive">
          <table className="table table-striped table-bordered">
            <thead>
              <tr>
                <th scope="col">Username</th>
                <th scope="col">신분 현황</th>
                <th scope="col">유물 5 갯수</th>
                <th scope="col">천왕 퀘스트 남은 갯수</th>
              </tr>
            </thead>
            <tbody>{rows}</tbody>
          </table>
        </div>
      </div>
    </Fragment>
  );
};

export default Home;
